@font-face {
    font-family: "Roboto-Regular"; /*a name to be used later*/
    src: url("../assets/Roboto-Regular.ttf"); /*URL to font*/
  }
  
  @font-face {
    font-family: "Roboto-Bold"; /*a name to be used later*/
    src: url("../assets/Roboto-Bold.ttf"); /*URL to font*/
  }
  
  .P21 {
    padding: 2rem 1rem 1rem 3rem;
    font-family: Roboto-Regular;
  }
  
  .P21-step {
    width: 80vw;
  }
  .P21-h1 {
    font-family: Roboto-Bold;
    font-size: 2.45rem;
    color: #8473ff;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
  }

  .P21-bigger{
    font-size: 3.2rem;
}
  
  .P21-text {
    padding-right: 35vw;
  }
  
  .P21-img {
    position: absolute;
    bottom: -4rem;
    right: 0rem;
  }
  
  .P21-imgS{
    position: absolute;
    bottom: -4rem;
    right: 3rem;
  }
  
  .P21-questions-container {
    margin: 0.5rem 0px 0px 0rem;
  }
  .P21-question {
    display: flex;
    margin-top: 0rem;
    padding: 0.22rem;
  }
  .P21-question-hover {
    display: flex;
    margin-top: 0rem;
    border: solid 0.2rem #8473ff;
    padding: 0.22rem;
  }
  .P21-label {
    font-size: 2rem;
    align-self: center;
    padding-left: 1rem;
  }
  