@font-face {
    font-family: "Roboto-Regular"; /*a name to be used later*/
    src: url("../assets/Roboto-Regular.ttf"); /*URL to font*/
  }
  
  @font-face {
    font-family: "Roboto-Bold"; /*a name to be used later*/
    src: url("../assets/Roboto-Bold.ttf"); /*URL to font*/
  }
  
  .P51 {
    padding: 2rem 1rem 1rem 3rem;
    font-family: Roboto-Regular;
  }
  
  .P51-step{
    width: 80vw;
  }
  .P51-h1 {
    font-size: 1.8rem;
    color: #8473ff;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
  }

.P51-bigger{
    font-size: 3.2rem;
}


  .P51-text {
    padding-right: 5vw;
  }
  
  .P51-img {
    position: absolute;
    bottom: -4rem;
    right: 0rem;
  }

  .P51-imgS{
    position: absolute;
    bottom: -4rem;
    right: 3rem;
  }
  
  .P51-questions-container{
    margin: 1rem 0vw 0px 0rem;
  }
  .P51-question{
    display: flex;
    padding: 0.5rem;
  }
  .P51-question-hover{
    display: flex;
    border: solid 0.2rem #8473ff;
    padding: 0.5rem;
  }
  .P51-label{
    font-size: 2rem;
    align-self: center;
    padding-left: 1rem;
  }
  